.App {
  text-align: center;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#Item{
  width: 40%;
  padding: 20px;
  margin: auto;
  /* justify-content: center; */
  justify-content: space-between;
  border: 1px solid black;

}
button{
  background: none;
  border: none;
}
.App {
  font-family: Arial, sans-serif;
}

.cart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  margin: auto;
  padding: 20px;
}

.cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.cart-item img {
  width: 100px;
}

.empty-cart-animation {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: red;
  animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
